.table {
  letter-spacing: 0.5px;
  color: rgb(32, 32, 32);
  width: 95%;
  margin: 25px auto;
  border-collapse: collapse;
  tr {
    font-weight: 300;
    border: 1px solid #ccc;
  }
  tr:nth-child(odd) {
    background-color: #f9f9f9;
  }
  td {
    padding: 8px;
  }
  &__title td {
    font-weight: 400;
    cursor: pointer;
  }
  &__icons {
    width: 30px;
    cursor: pointer;
  }
}
