@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@300;400;500;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header{display:flex;justify-content:space-between;align-items:center;width:100%;padding:25px;background-color:#fff;box-shadow:0px 3px 10px rgba(0,0,0,0.1);position:fixed;top:0;z-index:2}.header__logo{font-size:40px;font-weight:500;text-transform:uppercase;text-decoration:none;letter-spacing:5px;color:#000;position:absolute;left:50%;transform:translateX(-50%)}.header__info{display:flex;grid-gap:10px;gap:10px}.header__log-out-button{cursor:pointer}.header__button-menu,.header__button-globe{width:25px;height:25px;text-transform:uppercase;text-decoration:none;color:#000;cursor:pointer}.header__languages-list{position:relative}.header__button-globe{border:none;background:#fff;color:#2b2baf}.header__languages{position:absolute;right:0;border:1px #000 solid;border-radius:3px;background:#fff;overflow:hidden}.header__languages-item{display:flex;align-items:center;grid-gap:5px;gap:5px;width:100%;padding:10px 50px;border:none;background:#fff;cursor:pointer}.header__languages-item:hover{background:#ccc}.header__cart{text-transform:uppercase;text-decoration:none;color:#000;cursor:pointer}@media (max-width: 768px){.header__cart{margin:0 10px}.header__logo{display:none}}

.nav{max-width:25%;width:100%;height:100vh;background-color:#15171f;overflow:hidden;position:fixed;top:0;left:-25%;z-index:3;transition:0.5s}.nav__button-close{font-size:25px;color:#fff;position:absolute;top:10px;right:20px;cursor:pointer}.nav__list{margin-top:100px}.nav__list-link{display:block}.nav a{font-size:40px;font-weight:500;text-transform:uppercase;text-decoration:none;color:#fff}.nav a:hover,.nav a.selected{color:#fa5b2f}.nav__list-link-admin:hover li{display:block;line-height:40px}.nav__sub-nav{padding-left:30px;margin:0;position:absolute}.nav__sub-nav li{display:none;padding:0}.nav__sub-nav a{font-size:25px}.show{display:flex;justify-content:center;left:0}.back-bg{width:100%;height:100vh;background:#000;opacity:0.3;position:fixed;top:0;z-index:2}@media (max-width: 768px){.nav{display:none;max-width:80%}.show{display:flex}}

.link-button{font-weight:600;text-transform:uppercase;text-decoration:none;color:#fff;padding:20px 100px;background-color:#fa5b2f;white-space:nowrap;position:absolute;left:50%;bottom:50px;transform:translateX(-50%);z-index:1;transition:0.3s}.link-button:hover{color:#fa5b2f;background-color:#fff}.link-button__404{padding:15px 80px;border:3px #fa5b2f solid;bottom:120px}

.slider{width:100%;overflow:hidden;position:relative}.slider_slide{background-position:center;background-size:cover;height:100vh}.swiper-slide{height:100vh}.swiper-pagination{text-align:left;margin-left:20px;margin-bottom:5px}.swiper-pagination-bullet{width:18px;height:18px;background-color:#fff;opacity:1}.swiper-pagination-bullet-active{background-color:#fa5b2f}@media (min-width: 768){.swiper-pagination{text-align:left;margin-left:50px;margin-bottom:20px}}

.spinner-bg{width:100%;height:100%;position:absolute;top:0;left:0}.spinner{display:inline-block;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);width:80px;height:80px}.spinner div{position:absolute;width:6px;height:6px;background:#000;border-radius:50%;animation:spinner 1.2s linear infinite}.spinner div:nth-child(1){animation-delay:0s;top:37px;left:66px}.spinner div:nth-child(2){animation-delay:-0.1s;top:22px;left:62px}.spinner div:nth-child(3){animation-delay:-0.2s;top:11px;left:52px}.spinner div:nth-child(4){animation-delay:-0.3s;top:7px;left:37px}.spinner div:nth-child(5){animation-delay:-0.4s;top:11px;left:22px}.spinner div:nth-child(6){animation-delay:-0.5s;top:22px;left:11px}.spinner div:nth-child(7){animation-delay:-0.6s;top:37px;left:7px}.spinner div:nth-child(8){animation-delay:-0.7s;top:52px;left:11px}.spinner div:nth-child(9){animation-delay:-0.8s;top:62px;left:22px}.spinner div:nth-child(10){animation-delay:-0.9s;top:66px;left:37px}.spinner div:nth-child(11){animation-delay:-1s;top:62px;left:52px}.spinner div:nth-child(12){animation-delay:-1.1s;top:52px;left:62px}@keyframes spinner{0%,20%,80%,100%{transform:scale(1)}50%{transform:scale(1.5)}}

.products__item{flex-basis:33%;transition:0.5s;cursor:pointer;position:relative}.products__item-inner{display:block;width:100%;padding-bottom:100px;transition:0.5s}.products__item-inner img{width:100%}.products__item:hover .products__item-inner{transform:scale(1.1);box-shadow:0px 0px 15px rgba(195,195,195,0.6);background-color:#ffffff}.products__item:hover .products__item-descr{display:block}.products__item-descr{display:none;text-align:center;font-weight:500;position:absolute;left:50%;transform:translateX(-50%)}.products__item-title{font-size:25px;text-transform:uppercase}.products__item-price{font-size:18px;color:#fa5b2f;margin-top:10px}@media (max-width: 425px){.products__item{flex-basis:100%}}@media (min-width: 425px) and (max-width: 768px){.products__item{flex-basis:50%}}

.products{margin-top:120px}.products__sort{display:flex;justify-content:center;grid-gap:25px;gap:25px;margin:0 auto;padding:0 10px}.products__sort ion-icon{margin-left:20px}.products__sort-button{font-size:15px;letter-spacing:1px;font-weight:300}.products__sort-button select{font-size:15px;font-family:"Oswald";letter-spacing:1px;text-transform:uppercase;padding-right:25px;border-style:none;background-color:#f5f5f5;outline:none}.products__items{display:flex;flex-wrap:wrap;margin:50px}@media (max-width: 768px){.products__sort{flex-direction:column;grid-gap:5px;gap:5px}}

.about{display:flex;flex-direction:column;align-items:center;justify-content:center;height:100vh}.about__descr{max-width:1000px}@media (max-width: 765px){.about{margin-top:250px}}

.input{display:block;font-family:'Oswald';font-weight:bold;width:100%;height:50px;padding:0 10px;border:3px #000 solid;outline:none}.input input:focus,.input textarea:focus{border:3px #fa5b2f solid}.input textarea{height:200px;padding-top:10px;margin:15px auto 0;resize:none}

.form-button{display:block;font-size:16px;font-weight:600;text-transform:uppercase;color:#fff;width:300px;height:50px;margin-top:10px;margin-left:auto;background:#15171f;border:3px #15171f solid;transition:.5s;cursor:pointer}.form-button--width{width:100%}.form-button:hover{color:#000;background-color:#fff}

.contacts{display:flex;flex-direction:column;align-items:center;width:100%;padding:150px 20px}.contacts__form{max-width:1000px}@media (max-width: 768px){.contacts{padding:100px 20px}}

.cart{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.cart__link-back{display:block;text-align:center;text-decoration:none;color:#fa5b2f;margin-top:20px}.items{width:100%;max-width:1120px;margin:0 auto}.items-inner{display:flex;justify-content:space-between;flex-wrap:wrap;margin-top:150px}.item{width:30%;margin-bottom:50px;padding:50px;background-color:#fff;box-shadow:0px 0px 15px rgba(195,195,195,0.6);position:relative}.item__button-close{font-size:30px;position:absolute;top:5px;right:15px;cursor:pointer}.item__img{display:block;width:100%;margin:0 auto}.item__name{font-weight:400;text-align:center;text-transform:uppercase}.item__price{display:block;font-size:18px;text-align:center;color:#9e9d9d}.item__full-price{display:block;font-size:30px;color:#fa5b2f;text-align:center;margin-top:20px}.item__amount-product{display:flex;justify-content:space-between;width:100px;margin:0 auto;font-size:40px}.item__minus-amount,.item__plus-amount{font-size:35px;color:#ccc;cursor:pointer}.item__minus-amount:hover,.item__plus-amount:hover{color:#fa5b2f}.item__plus-amount{line-height:60px}.items__footer{display:flex;justify-content:space-between;align-items:center;width:100%;padding:10px;margin-bottom:50px;background-color:#fff;box-shadow:0px 0px 15px rgba(195,195,195,0.6)}.items__footer-price{font-size:25px;color:#7c7c7c}.items__footer-price-number{font-weight:600;color:#fa5b2f}.items__footer-button{font-weight:600;text-transform:uppercase;color:#fff;padding:15px 100px;background-color:#000;border:3px #000 solid;transition:0.3s;cursor:pointer}.items__footer-button:hover{color:#000;background-color:#fff}@media (max-width: 768px){.items-inner{margin:0 50px}.item{width:100%;margin-top:100px}.items__footer{flex-direction:column;grid-gap:20px;gap:20px;padding:50px 0}}

.product{height:100vh;padding-top:150px;background-image:linear-gradient(white, white);background-size:50% auto;background-position:right;background-repeat:no-repeat}.product__item{display:flex;flex-direction:row;align-items:center}.product__galery{width:50%}.product__galery-inner{width:70%;margin:0 auto}.product__galery img{width:100%}.product__more-img{display:flex;margin:0 80px}.product__more-img img{width:25%}.product__sub-img{margin:25px;border:1px #797979 solid;cursor:pointer}.product__descr{width:25%;margin:10px auto;padding:0 25px}.product__text{font-size:14px;font-weight:300;line-height:40px;letter-spacing:2px}.product__button{display:block;font-weight:500;text-align:center;text-transform:uppercase;color:#fff;width:100%;padding:10px;margin-top:30px;background-color:#15171f;border:3px #15171f solid;transition:0.5s;cursor:pointer}.product__button:hover{color:#000;background-color:#fff}@media (max-width: 768px){.product{padding-top:100px}.product__item{flex-direction:column}.product__galery{width:100%;margin:20px 0}.product__descr{width:100%}}

.toast{font-weight:bold;color:#fff;min-width:250px;padding:20px 50px;border-radius:5px;position:fixed;right:25px;top:25px;z-index:2;visibility:hidden}.toast__success{background-color:#4caf50}.toast__error{background-color:red}.toast__show{visibility:visible;animation:fadein 0.5s, fadeout 0.5s 2.6s}@keyframes fadein{from{opacity:0}to{opacity:1}}@keyframes fadeout{from{opacity:1}to{opacity:0}}

.sign-in{max-width:500px;margin:150px auto 10px;padding:0 10px}.sign-in__button{display:flex;align-items:center;justify-content:center;grid-gap:10px;gap:10px;border-radius:3px;width:100%;padding:10px 0;background:#fff;font-size:16px;cursor:pointer}.sign-in__button-logo{width:20px}.sign-in__button:hover{background:#eee}.sign-in__registration-button{display:block;text-decoration:underline;margin-top:10px}.sign-in__registration-button:hover{text-decoration:none}@media (max-width: 768px){.sign-in{margin-top:100px}}

.registration{max-width:500px;margin:150px auto 10px;padding:0 10px}@media (max-width: 768px){.registration{margin-top:100px}}

.nav-admin{height:100vh;padding-top:30px;background-color:#3a3f51}.nav-admin__logo{font-size:30px;font-weight:bold;color:#fff;padding-left:20px}.nav-admin__list{margin:20px auto}.nav-admin__list a{display:block;color:#d8d8d8;letter-spacing:1px;width:200px;padding:10px 20px;transition:.3s}.nav-admin__list a:hover{color:#fff;background-color:#2c303f}.nav-admin__selected{color:#fff;background-color:#2c303f}

.header-admin{display:flex;justify-content:flex-end;padding:10px 10px;border-bottom:1px #161616 solid}.header-admin__info{display:flex;align-items:center}.header-admin__name{margin-right:5px}.imageBoundary{display:inline-block;width:35px;height:35px;margin-right:10px;border:1px #ccc solid;border-radius:100%;overflow:hidden}.imageBoundary img{width:100%}

.admin-catalog{display:flex;align-items:flex-start;padding:50px}.admin-catalog__setting{width:300px;padding:10px 20px;margin:25px;border:1px #b8b8b8 solid;border-radius:10px}.admin-catalog__setting h2{font-size:16px;font-weight:400;margin-top:20px}.admin-catalog__setting-search{width:100%;padding:10px;border:none;border-bottom:1px #000 solid}.admin-catalog__setting-list{padding:0 40px}.admin-catalog__setting-list li{font-weight:300;margin:5px 0;cursor:pointer}.active{font-weight:400 !important}

.admin{display:flex}.admin__component{width:100%}

.table{letter-spacing:0.5px;color:#202020;width:95%;margin:25px auto;border-collapse:collapse}.table tr{font-weight:300;border:1px solid #ccc}.table tr:nth-child(odd){background-color:#f9f9f9}.table td{padding:8px}.table__title td{font-weight:400;cursor:pointer}.table__icons{width:30px;cursor:pointer}

.form-admin{width:500px;margin:100px auto}.form-admin__field{width:100%;padding:15px 10px;margin-bottom:10px;background-color:#fff;border:none;border-bottom:1px #000 solid}.form-admin__half-width{display:flex;justify-content:space-between}.form-admin__half-width .form-field-admin{width:48%}.form-admin__select-photo{display:block;padding-bottom:15px}.form-admin__button{font-weight:bold;text-transform:uppercase;padding:8px 25px;background-color:#fff;border:1px #000 solid;border-radius:5px;cursor:pointer}.form-field-admin{width:100%;margin-bottom:20px}.error-message-admin{font-size:14px;color:#ca1a1a}

*{margin:0;padding:0;box-sizing:border-box}body{font-family:'Oswald';background-color:#f5f5f5}a{text-decoration:none;color:#000}li{list-style-type:none}.container{max-width:980px;margin:0 auto;padding:0 20px}

.descr{display:block;font-size:16px;font-weight:300;line-height:40px;letter-spacing:2px;text-align:center;margin-top:10px;padding:0 20px}.descr--align-left{text-align:start}.descr--red{font-size:25px;font-weight:600;color:#fa5b2f}.descr--letter-spacing{font-size:16px;font-weight:400;letter-spacing:5px}

.title{font-size:40px;text-align:center}.title--uppercase{font-size:50px;text-transform:uppercase;color:#15171f}

.form__info{display:flex;flex-wrap:wrap;justify-content:space-between;margin-top:25px}.form textarea{display:block;font-family:'Oswald';font-weight:bold;width:100%;height:200px;padding:10px 10px;margin:15px auto 0;border:3px #000 solid;outline:none;resize:none}.error-message{color:#fa5b2f;margin:5px 0}

